@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
.but {
	background-color: #27CCC0;
	width: 35%;
}

.isActive::before {
	content: " ";
	position: absolute;
	left: -35px;
	display: block;
	height: 100%;
	width: 1px;
	border-left: 5px solid red;
}
.notes_cont {
  padding: 30px 0 30px 30px;
  align-items: flex-start;
}
.note_comment {
  border: 2px solid #f2f2f2;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
}
.note_comment h2 {
  color: #565a69;
}
.note_comment:first-child {
  margin-right: 20px;
  padding: 30px 20px 20px 30px;
  min-height: 540px;
}
.note_comment:last-child {
  padding: 20px;
  margin-right: 20px;
}

#root {
  font-family: 'Open Sans', sans-serif;
}
.App {
  display: flex;
}
.sidebar {
  background-color: #2b2f3e;
  color: white;
  min-height: 100vh;
  padding: 2.5vw 0 0 2.5vw;
  width: 20%;
}
.sidebar h6 {
  color: #808080;
}
.main {
  width: 80%;
  height: 100vh;
  overflow: scroll;
}
ul {
  list-style: none;
  padding-left: 0;
}

@media screen and (max-width: 1100px) {
  .sidebar {
    padding: 1vw 0 0 1vw !important;
  }
  .sidebar h3,
  h6 {
    font-size: 0.9em;
  }
}

@media (orientation: portrait) {
  .App {
    flex-direction: column;
    height: 100vh;
  }
  .sidebar {
    min-height: auto;
    width: 100%;
  }
  .main {
    width: 100%;
    height: auto;
  }
  .note_comment {
    margin: 2vw !important;
  }
  .notes_cont {
    padding: 0;
  }
  .note_comment:first-child {
    min-height: auto;
  }
}

